@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap);
*,
*::after,
*::before {
  box-sizing: border-box;
}

body,
html {
  margin: 0;
  padding: 0;
  font-family: "Rubik", sans-serif;
}

.standard-p {
  color: hsl(229, 8%, 60%);
  font-size: 1.2rem;
  line-height: 1.7rem;
}

.standard-heading {
  font-size: 2rem;
  color: hsl(229, 31%, 21%);
}

.section-global {
  width: 90%;
  margin: auto;
  text-align: center;
  max-width: 1500px;
}

@media (min-width: 1500px) {
  .section-global {
    width: 100%;
  }
}

.App {
  overflow: hidden;
}

.btn {
  padding: 1rem 1.5rem;
  font-weight: 500;
  font-family: "Rubik", sans-serif;
  border-radius: 5px;
  border: none;
  margin: 1rem;
  border: 1px solid transparent;
}
.btn:hover {
  cursor: pointer;
}

.btn-blue {
  background-color: hsl(231, 69%, 60%);
  color: white;
}
.btn-blue:hover {
  background-color: white;
  color: hsl(231, 69%, 60%);
  border: 1px hsl(231, 69%, 60%) solid;
}

.btn-white {
  color: hsl(229, 31%, 21%);
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}
.btn-white:hover {
  background-color: white;
  color: hsl(229, 31%, 21%);
  border: 1px hsl(229, 31%, 21%) solid;
}

.btn-red {
  background-color: hsl(0, 94%, 66%);
  letter-spacing: 3px;
  color: white;
}
.btn-red:hover {
  background-color: white;
  color: hsl(0, 94%, 66%);
  border: 1px hsl(0, 94%, 66%) solid;
}
@media (min-width: 768px) {
  .btn-m-l {
    margin: 1rem 1rem 1rem 0;
  }
}

.download-section {
  margin-top: 7rem;
}

.browser-cards-container {
  max-width: 1200px;
  margin: 2rem auto;
}
.browser-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 3rem;
  max-width: 300px;
  padding: 1rem;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
}
.browser-info {
  width: 100%;
  background: url(/bookmark-landing-page-react/static/media/bg-dots.90f0b46c.svg) no-repeat bottom center/contain;
  padding-bottom: 1.5rem;
}
.browser-heading {
  color: hsl(229, 31%, 21%);
  font-size: 1.2rem;
  font-weight: 600;
  margin: 2rem 0 0 0;
}

@media (min-width: 768px) {
  .browser-cards-container {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .browser-info.btn-mobile.btn {
    margin: 2rem 0;
  }
  .browser-card {
    width: 100%;
  }
  .browser-card:nth-child(2) {
    margin-top: 5rem;
  }
  .browser-card:nth-child(3) {
    margin-top: 7rem;
  }
}

/* CHOICE BTNS */
.choice-btns {
  margin: 5rem auto;
}
.feature-btn {
  font-family: "Rubik", sans-serif;
  color: hsl(229, 8%, 60%);
  font-weight: 500;
  width: 100%;
  padding: 1.5rem 0;
  font-size: 1.2rem;
  border: none;
  border-top: 1px rgb(209, 209, 209) solid;
  background-color: white;
  cursor: pointer;
}
.feature-btn:last-child {
  border-bottom: 1px rgb(209, 209, 209) solid;
}
.feature-btn.active {
  color: hsl(229, 31%, 21%);
  border-bottom: 5px hsl(0, 94%, 66%) solid;
}

/* CHOSEN CARD */
.img-container {
  padding: 2rem 0 3rem 0;
  position: relative;
}
.img-background2 {
  position: absolute;
  background-color: hsl(231, 69%, 60%);
  border-radius: 0 0 8rem 0;
  top: 25%;
  left: -100%;
  right: 20%;
  bottom: 0;
  z-index: -1;
}

.card-img {
  width: 100%;
  height: 100%;
}

@media (min-width: 768px) {
  .text-container {
    width: 70%;
    max-width: 650px;
    margin: auto;
  }
  .choice-btns {
    display: flex;
    max-width: 1000px;
  }
  .feature-btn {
    border-top: none;
    border-bottom: 1px rgb(209, 209, 209) solid;
  }
  .chosen-card {
    display: flex;
    text-align: left;
    align-items: center;
    justify-content: center;
  }
  .chosen-card > * {
    flex-basis: 50%;
  }
  .card-text-container {
    margin-left: 5rem;
  }
}

.footer {
  background-color: hsl(229, 31%, 21%);
  padding: 2rem 0;
}
.footer-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.footer-logo {
  padding: 1rem 0;
}
.footer-item {
  margin: 1rem auto;
  color: white;
  text-decoration: none;
  letter-spacing: 0.15rem;
}
.socials-container {
  margin-top: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
}

@media (min-width: 768px) {
  .footer-container {
    flex-direction: row;
    justify-content: space-between;
  }
  .footer-list {
    flex-direction: row;
    grid-gap: 3rem;
    gap: 3rem;
  }
  .socials-container {
    margin-top: 0;
  }
}

/* HERO SECTION */
/* HERO IMG  */
.hero-img-container {
  position: relative;
  padding: 2rem 0;
}

.img-background {
  position: absolute;
  background-color: hsl(231, 69%, 60%);
  border-radius: 0 0 0 8rem;
  top: 25%;
  left: 20%;
  right: -100%;
  bottom: 0;
  z-index: -1;
}

.hero-img {
  width: 100%;
  height: 100%;
}
/* HERO TEXT */
.hero-text {
  padding: 2rem 0;
  text-align: center;
}

.hero-heading {
  color: hsl(229, 31%, 21%);
  font-size: 2.2rem;
}

.buttons-container {
  padding: 1rem 0;
}

@media (min-width: 768px) {
  .hero-container {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }
  .hero-container > * {
    flex: 1 1 50%;
  }
  .hero-text {
    text-align: left;
  }
  .hero-heading {
    font-size: 2.6rem;
  }
}

.navbar {
  background-color: #fff;
  margin: auto;
  max-width: 1500px;
  transition: all 0.5s ease-in-out;
}

.navbar-open {
  background-color: hsla(229, 31%, 21%, 0.98);
}

.navbar-container {
  margin: auto;
  width: 90%;
  height: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav-menu {
  background-color: hsla(229, 31%, 21%, 0.98);
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: calc(100vh - 6rem);
  position: absolute;
  top: 6rem;
  left: -100%;
  transition: all 1s ease;
}

.nav-menu.active {
  left: 0;
  transition: all 0.5s ease;
  z-index: 1;
}

.nav-item {
  margin: 0 auto;
  width: 90%;
  border-top: 1px hsla(229, 8%, 60%, 0.3) solid;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  padding: 2rem;
  font-size: 1.7rem;
  letter-spacing: 3px;
}

.nav-links:hover {
  color: hsl(0, 94%, 66%);
}

.nav-links-mobile {
  display: block;
  text-align: center;
  margin: 2rem auto;
  border-radius: 4px;
  width: 80%;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 500;
  background-color: transparent;
  color: #fff;
  padding: 14px 20px;
  border: 2px solid #fff;
  transition: all 0.3s ease-out;
  letter-spacing: 3px;
}

.nav-links-mobile:hover {
  color: hsl(0, 94%, 66%);
  border: 2px solid hsl(0, 94%, 66%);
  transition: 250ms;
}

.menu-hamburger {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.icon-hamburger {
  width: 40px;
  height: 4px;
  background: hsl(229, 31%, 21%);
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}

.icon-hamburger::before,
.icon-hamburger::after {
  content: "";
  position: absolute;
  width: 40px;
  height: 4px;
  background: hsl(229, 31%, 21%);
  border-radius: 5px;
  transition: all 0.5s ease-in-out;
}

.icon-hamburger::before {
  -webkit-transform: translateY(-12px);
          transform: translateY(-12px);
}

.icon-hamburger::after {
  -webkit-transform: translateY(12px);
          transform: translateY(12px);
}

.menu-hamburger.open .icon-hamburger {
  -webkit-transform: translateX(-50px);
          transform: translateX(-50px);
  background: transparent;
}

.menu-hamburger.open .icon-hamburger::before {
  -webkit-transform: rotate(45deg) translate(35px, -35px);
          transform: rotate(45deg) translate(35px, -35px);
  background: #fff;
}

.menu-hamburger.open .icon-hamburger::after {
  -webkit-transform: rotate(-45deg) translate(35px, 35px);
          transform: rotate(-45deg) translate(35px, 35px);
  background: #fff;
}

@media (min-width: 768px) {
  .menu-hamburger {
    display: none;
  }

  .navbar-open {
    background-color: #fff;
  }

  .navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .nav-menu {
    background-color: transparent;
    position: relative;
    width: unset;
    flex-direction: row;
    left: unset;
    height: unset;
    top: unset;
  }

  .nav-item {
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .nav-links {
    color: hsl(229, 31%, 21%);
    font-size: 1.1rem;
    padding: 1.5rem;
    letter-spacing: 1px;
  }

  .nav-links-mobile {
    display: none;
  }
}

@media (min-width: 1500px) {
  .navbar-container {
    width: 100%;
  }
}

.newsletter {
  background-color: hsl(231, 69%, 60%);
  padding: 3rem 0;
  margin-top: 7rem;
}
.news-wrapper {
  max-width: 700px;
}
.news-heading {
  font-size: 2rem;
  font-weight: 500;
  color: white;
}
.news-p {
  color: white;
  letter-spacing: 0.4rem;
}

.news-form {
  display: flex;
  flex-direction: column;
}
.email-input {
  font-family: "Rubik", sans-serif;
  color: hsl(229, 31%, 21%);
  font-weight: 500;
  padding: 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 5px;
  width: 100%;
}

.input-wrapper {
  position: relative;
}
.input-wrapper.invalid {
  border: 4px hsl(0, 94%, 66%) solid;
  border-radius: 10px;
}
.input-wrapper.invalid::after {
  content: "!";
  font-size: 1.5rem;
  color: white;
  position: absolute;
  top: calc(35% - 15px);
  right: 5%;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: hsl(0, 94%, 66%);
}

.error {
  font-size: 1.1rem;
  color: white;
  margin: 0;
  background-color: hsl(0, 94%, 66%);
  text-align: left;
}

.news-btn {
  width: 100%;
  margin: 2rem auto;
  font-size: 1rem;
}

@media (min-width: 500px) {
  .news-form {
    flex-direction: row;
    align-items: center;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .input-wrapper {
    flex-grow: 1;
    max-width: 400px;
  }
}

.questions-section {
  margin-top: 7rem;
}

/* ACCORDION FAQ */
.accordion-container {
  width: 100%;
  margin: 5rem auto 3rem auto;
  max-width: 700px;
}

.question {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px rgb(209, 209, 209) solid;
  cursor: pointer;
}
.question-text {
  font-size: 1.2rem;
  color: hsl(229, 31%, 21%);
  font-weight: 500;
}
.answer {
  color: hsl(234, 5%, 44%);
  text-align: left;
  line-height: 1.7rem;
  overflow: hidden;
  max-height: 0;
  transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
}
.answer.active {
  max-height: 999px;
  transition: max-height 0.5s cubic-bezier(1, 0, 1, 0);
}

.arrow-img {
  transition: all 0.5s ease-in;
}
.rotate {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  transition: all 0.5s ease-in;
}

